<template>
  <div class="screening">
    <div class="head">
      <a @click="_back" class="left"><i class="back"></i>返回</a>
      <p>更多条件</p>
    </div>
    <div class="conditionFilter weui_tab_bd">
      <article class="weui_filter">
        <section>
          <div class="title-top">按时间筛选</div>
          <ul class="filterList">
            <li><a href="javascript:;" :class="{'actives': activeAll}" @click="querycar('all')">所有时间</a></li>
            <li><a href="javascript:;" :class="{'actives': active0}" @click="querycar('0')">今天</a></li>
            <li><a href="javascript:;" :class="{'actives': active1}" @click="querycar('-1')">昨天</a></li>
            <li><a href="javascript:;" :class="{'actives': active2}" @click="querycar('-2')">近三天</a></li>
            <li><a href="javascript:;" :class="{'actives': active6}" @click="querycar('-6')">近七天</a></li>
            <li><a href="javascript:;" :class="{'actives': active29}" @click="querycar('-29')">近一个月</a></li>
          </ul>
        </section>
        <section v-if="boolert">
          <div class="title-top">按状态筛选</div>
          <ul class="filterList">
            <li><a href="javascript:;" :class="{'actives': stateAll}"  @click="querycar('stateAll')">全部状态</a></li>
            <li><a href="javascript:;" :class="{'actives': accepted}"  @click="querycar('accepted')">已修复</a></li>
            <li><a href="javascript:;" :class="{'actives': noAccepted}"  @click="querycar('noAccepted')">未修复</a></li>
          </ul>
        </section>
      </article>
      <div class="footerFilter">
        <a class="weui_btn weui_btn_primary closed" @click="closed" style="color: #333" href="javascript:;">取消</a>
        <a class="weui_btn weui_btn_primary" @click="determine()" href="javascript:;">确定</a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      activeAll:  true,
      active0: false,
      active1: false,
      active2: false,
      active6: false,
      active29: false,
      stateAll: true,
      accepted: false,
      noAccepted: false,
      boolert: false
    }
  },
  created () {
    this._state();
  },
  methods: {
    _state () {
      let boolert = this.$route.query.boolert
      let timeTyple = localStorage.timeTyple
      let stateTyple = localStorage.stateTyple
      this.boolert = boolert
      localStorage.boolerts = boolert
      if(timeTyple){
        this.querycar(timeTyple)
      }
      if(stateTyple&&boolert) {
        if(stateTyple == "U") {
          this.querycar('noAccepted')
        } else if(stateTyple == "D") {
          this.querycar('accepted')
        }else {
          this.querycar(timeTyple)
        }
      }
    },
    _back() {
      this.$router.go(-1);
    },
    downList() {
      this.listBooler = true;
      setTimeout(() => {
        this.listBooler = false
      }, 2000);
    },
    querycar (expression) {
      switch (expression){
        case 'all':
          this.activeAll = true;
          this.active0 = false;
          this.active1 = false;
          this.active2 = false;
          this.active6 = false;
          this.active29 = false;
          localStorage.timeTyple = 'all'
        break
        case '0':
          this.activeAll = false;
          this.active0 = true;
          this.active1 = false;
          this.active2 = false;
          this.active6 = false;
          this.active29 = false;
          localStorage.timeTyple = 0
        break
        case '-1':
          this.activeAll = false;
          this.active0 = false;
          this.active1 = true;
          this.active2 = false;
          this.active6 = false;
          this.active29 = false;
          localStorage.timeTyple = '-1'
        break
        case '-2':
          this.activeAll = false;
          this.active0 = false;
          this.active1 = false;
          this.active2 = true;
          this.active6 = false;
          this.active29 = false;
          localStorage.timeTyple = '-2'
        break
        case '-6':
          this.activeAll = false;
          this.active0 = false;
          this.active1 = false;
          this.active2 = false;
          this.active6 = true;
          this.active29 = false;
          localStorage.timeTyple = '-6'
        break
        case '-29':
          this.activeAll = false;
          this.active0 = false;
          this.active1 = false;
          this.active2 = false;
          this.active6 = false;
          this.active29 = true;
          localStorage.timeTyple =' -29'
        break
        case 'stateAll':
          this.stateAll = true;
          this.accepted = false;
          this.noAccepted = false;
          localStorage.stateTyple = 'stateAll'
        break
        case 'accepted':
          this.stateAll = false;
          this.accepted = true;
          this.noAccepted = false;
          localStorage.stateTyple = 'D'
        break
        case 'noAccepted':
          this.stateAll = false;
          this.accepted = false;
          this.noAccepted = true;
          localStorage.stateTyple = 'U'
        break
      }
    },
    closed () {
        this.$router.go(-1);
        // localStorage.timeTyple = ''
        // localStorage.stateTyple = ''
        // localStorage.boolerts = false
    },
    determine () {
      this.$router.push({path: '/venueIndex'})
      localStorage.determineBooler = true
    }
  },
}
</script>
<style lang="less" scoped>
.screening {
  height: 100%;
  background: #f6f6f6
}
.head {
  height: 2.8125rem /* 45/16 */;
  background: #1f95ff;
  display: flex;
  justify-content: space-between;
  p {
    color: #fff;
    line-height: 2.8125rem; 
    position: absolute;
    left: 50%;
  }
  .left {
    display: inline-block;
    height: 100%;
    width: 84px;
    text-indent: 37px;
    line-height: 45px;
    position: relative;
    color: #fff;
    .back {
      content: "";
      display: block;
      position: absolute;
      border-left: 2px solid #fff;
      border-top: 2px solid #fff;
      transform: rotate(-45deg);
      width: 12px;
      height: 12px;
      left: 20px;
      top: 50%;
      margin-top: -7px;
    }
  }
}
.weui_tab_bd{
  // background: #f5f7fa;
  section {
    background: #f6f6f6
  }
  .title-top {
    /* text-align: center; */
    background: #f6f6f6;
    margin: 0;
    padding: 18px 0 10px 16px;
    font-size: 14px;
    color: #7a7e80;
  }
  .filterList {
    background: #fff;
    padding: 0 15px 15px;
    li {
      display: inline-block;
      margin: 15px 10px 0 0;
      a {
        display: block;
        padding: 5px 18px;
        background: #e6eaf1;
        border-radius: 5px;
        color: #333;
        &.actives {
          background: #1f95ff;
          color: #fff;
        }
      }
    }
  }
  .footerFilter {
    width: 100%;
    background: #fff;
    display: flex;
    position: absolute;
    bottom: 0;
    a {
      flex: 1;
      margin: 6px 10px 6px;
      border-radius: 10px;
      padding: 9px;
      font-size: 19px;
      text-align: center;
      &.weui_btn_primary {
        background-color: #1f95ff;
        color: #FFF;
      }
      &.closed {
        border:1px solid rgba(0,0,0,.2);
        background: #fff;
        color: #333;
      }
    }
  }
}
</style>



